.catalog-page {
  padding: 64px 0 160px;

  &__models {
    width: 100%;
    padding-left: 40px;

    @include big-desktop {
      padding-left: 0;
    }
  }

  &__inner {
    display: flex;
  }

  & .wrapper-grid {
    grid-template-columns: repeat(12, 1fr);

    @include tablet {
      grid-template-columns: repeat(8, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  & .card {
    grid-column: 4 span;
    box-shadow: 0px 0px 0px 0px rgba(186, 186, 186, 0.10), 2px 9px 21px 0px rgba(186, 186, 186, 0.10), 6px 38px 38px 0px rgba(186, 186, 186, 0.09), 14px 84px 51px 0px rgba(186, 186, 186, 0.05), 25px 150px 61px 0px rgba(186, 186, 186, 0.01), 39px 235px 67px 0px rgba(186, 186, 186, 0.00);
  }
}

.catalog-page .graph-modal {
  position: static;
  flex: 0 0 290px;
  display: flex;
  visibility: visible;
  z-index: 0;
  background-color: transparent;
}

.catalog-page .graph-modal.is-open {
  opacity: 1;
}

.catalog-page .graph-modal__container {
  position: static;
  display: block;
  width: auto;
  padding: 0;
  margin: 0;
}

.withdrawal-goods {

  &__select {
    background-image: url('../img/svg-icon/select-arrow.svg');
    background-repeat: no-repeat;
    background-size: 18px;
    background-position: 95% 50%;
    padding: 20px 16px;
    padding-right: 45px;
    border-radius: 10px;
    border: 1px solid var(--3-grey);
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    appearance: none;
  }

  &__option {
    display: inline-block;
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    border-radius: 10px;
    margin-bottom: 11px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__btn {
    display: none;
    border-radius: 10px;
    border: 1px solid var(--3-grey);
    padding: 12px 16px;

    @include big-desktop {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.filter-container {
  display: flex;
  align-items: stretch;
  gap: 21px;
}

.cards-row {

  &__btn {
  }
}

.cursor-click {
}

.cursor-click--track {
}

.stock-card {
  margin-bottom: auto;
  min-height: 373px;
  padding: 35px 20px;
  background-color: var(--bl);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center right;

  &__info {
  }

  &__promotion {
    display: block;
    color: var(--1-grey);
    font-family: var(--font-Inter);
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.24px;
  }

  &__strong {
    display: block;
    color: var(--white);
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 3.2px;
  }

  &__what-discount {
    color: var(--orange);
    font-family: var(--font-Bebas);
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 2px;
  }
}

.grid-filter {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.product-filter {
  max-width: 290px;
  margin: 0;
}

.hidden {
  display: none;
}
