.our-business {
  padding: 40px 0 80px;

  @include tablet {
    padding: 40px 0;
  }

  & .container-right {
    padding-left: 0;
  }

  &__inner {
    display: flex;
    align-items: stretch;

    @include for-desktop {
      padding: 0 15px;
      flex-direction: column;
    }
  }

  &__box {
    width: 50%;

    @include for-desktop {
      width: 100%;
    }
  }

  &__info {
    background-color: var(--bl);
    color: var(--white);
    padding: 85px;

    @include xxx-desktop {
      padding: 45px;
    }

    @include big-desktop {
      padding: 30px;
    }
  }

  &__content {
    width: 100%;
    max-width: 556px;
    margin-left: auto;

    @include for-desktop {
      max-width: none;
      margin-left: 0;
    }
  }

  &__title {
    color: var(--white);
    margin-bottom: 32px;

    @include xxx-desktop {
      font-size: 35px;
      margin-bottom: 20px;
    }

    @include big-desktop {
      font-size: 25px;
      margin-bottom: 15px;
    }

    @include for-desktop {
      margin-bottom: 25px;
    }
  }

  &__container-text {
    margin-bottom: 40px;

    @include big-desktop {
      margin-bottom: 15px;
    }

    @include for-desktop {
      margin-bottom: 25px;
    }
  }

  &__text {
    color: var(--3-grey);

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__box--first {
    padding: 50px 0;

    @include for-desktop {
      padding: 0;
      margin-bottom: 30px;
      border-radius: 10px;
      overflow: hidden;
    }
  }

  &__picture {
    display: block;
    height: 100%;
  }

  &__image {
    border-radius: 10px;
    height: 100%;

    @include for-desktop {
      width: 100%;
      height: 500px;
    }
  }
}

