.about-store {
  padding: 80px 0 160px;

  @include tablet {
    padding: 40px 0 100px;
  }

  &__title {
    margin-bottom: 48px;

    @include xxx-desktop {
      font-size: 50px;
    }

    @include for-desktop {
      margin-bottom: 30px;
    }

    @include tablet {
      font-size: 40px;
    }

    @include small-tablet {
      font-size: 31px;
    }
  }

  &__inner {
    display: flex;
    gap: 20px;
    margin-bottom: 40px;

    @include for-desktop {
      flex-wrap: wrap;
    }
  }

  &__description {
  }

  &__wrapper {

    @include xxx-desktop {
      padding: 0 15px;
    }
  }

  &__info {
    color: var(--2-grey);
  }

  &__bg {
    width: 100%;
    height: 533px;
    margin-bottom: 56px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @include for-desktop {
      height: 400px;
    }

    @include tablet {
      height: 350px;
    }

    @include small-tablet {
      height: 300px;
    }
  }

  &__heading {
    display: inline-block;
    margin-bottom: 48px;
    font-family: var(--font-Inter);
    font-size: 24px;

    @include tablet {
      font-size: 20px;
      margin-bottom: 25px;
    }
  }

  &__slide {
    padding: 40px;
    border-radius: 10px;
    color: var(--white);
    width: 100%;

    @include small-tablet {
      padding: 25px;
    }
  }

  &__slide--bicicles {
    background-color: var(--dark-grey);
  }

  &__slide--accessories {
    background-color: var(--bl);
  }

  &__slide--spare-parts {
    background-color: var(--orange);
  }

  &__slide--equipment {
    background-color: var(--1-grey);
  }

  &__caption {
    margin-bottom: 24px;
    font-family: var(--font-Inter);
    font-size: 24px;
    color: var(--white);

    @include big-desktop {
      font-size: 20px;
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;

    @include small-tablet {
      column-gap: 15px;
      row-gap: 5px;
    }
  }

  &__item {
  }

  &__link {
    font-weight: 300;

    &:hover {
      text-decoration: underline;
    }
  }
}


