.latest-reviews {
  padding: 105px 0 110px;

  @include xxx-desktop {
    padding-left: 15px;
  }

  @include tablet {
    padding: 70px 0 70px;
    padding-left: 15px;
  }

  @include mobile {
    padding-left: 15px;
    padding-right: 15px;
  }

  &__subtitle {
    margin-bottom: 64px;

    @include big-desktop {
      margin-bottom: 30px;
    }
  }

}

.card-reviews {
  display: flex;
  flex-direction: column;
  background-color: var(--bl);
  border-radius: 10px;
  overflow: hidden;

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    color: var(--white);
    padding: 24px 30px 39px 27px;
    letter-spacing: 0px;
  }

  &__time {
    display: block;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    margin-bottom: 8px;
    letter-spacing: 0px;
  }

  &__hashtag {
    display: block;
    color: var(--orange);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    margin-bottom: 16px;
  }

  &__text {
    flex: 1 1 auto;
    text-overflow: ellipsis;
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    letter-spacing: 0.18px;
    margin-bottom: 21px;
    font-family: var(--font-Inter);
    color: var(--white);

    @include tablet {
      font-size: 16px;
    }
  }

}

.swiper-slide-latest-reviews {
  width: 100%;
  max-width: 400px;
}

.swiper-latest-reviews {
  margin-bottom: 47px;
}
