.storage-hero {
  padding: 50px 0 160px;

  @include xxx-desktop {
    padding: 50px 0 100px;
  }
  @include big-desktop {
    padding: 50px 0 70px;
  }

  @include tablet {
    padding: 50px 0;
  }

  &__text {
    margin-bottom: 64px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 40px;

    @include xxx-desktop {
      gap: 25px;
    }

    @include tablet {
      flex-direction: column;
    }
  }

  &__price {
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
    background-color: var(--orange);
    padding: 25px;
    color: var(--white);

    @include xxx-desktop {
      max-width: none;
    }
  }

  &__heading {
    display: block;
    padding-bottom: 24px;
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 500;
    border-bottom: 1px solid rgba(255, 255, 255, 0.20);

    @include xxx-desktop {
      padding-bottom: 16px;
      margin-bottom: 18px;
    }

    @include tablet {
      font-size: 17px;
    }
  }

  &__present {
    font-size: 15px;
    font-weight: 300;
  }

  &__description-text {
    border-radius: 10px;
    background-color: var(--dark-grey);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 100% 100%;
    padding: 47px 68px;
    color: var(--white);
    line-height: 200%;

    @include xxx-desktop {
      width: 100%;
      padding: 27px 48px;
      line-height: 160%;
    }

    @include for-desktop {
      padding: 25px;
    }
  }
}
