.winter-ride {
  background-color: var(--bl);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding: 75px 15px 70px;

  &__subtitle {
    font-family: var(--font-Bebas);
    color: var(--white);
    margin-bottom: -61px;

    @include tablet {
      margin-bottom: -41px;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  & .container-fluid {
    background-repeat: no-repeat;
    background-size: 541px 402px;
    background-position: 25px 276px;
  }

}

.swiper--winter {
  margin-left: 316px;

  @include for-desktop {
    margin-left: 150px;
  }

  @include small-tablet {
    margin-left: 0px;
  }
}

.winter-ride .card {
  box-shadow: none;

  &__header {
    margin-bottom: 13px;
  }

  &__moodel {
    padding-top: 13px;
    padding-bottom: 24px;
  }

  &__model-name {
    margin-bottom: 26px;
  }

}

