.guarantees-hero {
  padding: 80px 0;

  @include for-desktop {
    padding: 60px 0;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 80px;

    @include for-desktop {
      gap: 50px;
    }

    @include tablet {
      gap: 25px;
      flex-wrap: wrap;
    }
  }

  &__text {
    color: var(--1-grey);

    @include mobile {
      font-size: 14px;
    }
  }
}
