.menu-wrapper {
  position: relative;
  background-color: var(--white);
  z-index: 998;
  width: 100%;
  max-width: 23.2%;
  transform: translateX(100%);
  margin-left: auto;
  min-height: 100%;
  padding: 104px 60px 30px 40px;
  transition-duration: 0.3s;
	transition-property: transform;
	transition-delay: 0s;

  @include small-tablet {
    padding: 104px 60px 30px 30px;
  }
}

.menu {
  visibility: hidden;
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100%;
  top: 0%;
  right: 0%;
  overflow-y: auto;
  overflow-x: hidden;
  transition-duration: 0.3s, 0.3s;
  transition-property: visibility, right;
  transition-delay: 0s, 0s;

  &__subtitle {
    display: block;
    font-family: var(--font-Bebas);
    color: var(--bl);
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 2px;
    margin-bottom: 48px;

    @include small-tablet {
      font-size: 35px;
      margin-bottom: 20px;
    }
  }

}

.menu--active {
  visibility: visible;
  background-color: rgba($color: #000000, $alpha: 0.3);
  backdrop-filter: blur(3px);
}

.menu--active .menu-wrapper {
  transform: translateX(0%);
  min-width: 400px;

  @include mobile {
    min-width: auto;
    max-width: 340px;
  }
}

.menu .nav {

  &__list {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  &__item {
    margin-right: 0;
    margin-bottom: 24px;

    @include small-tablet {
      margin-bottom: 15px;
    }

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.18px;

    @include small-tablet {
      font-size: 14px;
      line-height: 145%;
    }

    &:hover {
      color: var(--orange);
    }
  }

}

.burger {
position: relative;
width: 32px;
height: 18px;
cursor: pointer;
transition-duration: 0.2s;
transition-property: opacity;
transition-delay: 0s;

  &:hover {
    opacity: 0.7;
  }

  &:before,
  &:after {
    content: '';
    position: absolute;
    right: 0;
    width: 24px;
    height: 2px;
    border-radius: 10px;
    background-color: var(--white);
    transition-duration: 0.2s;
    transition-property: all;
    transition-delay: 0s;
  }

  &:before {
    top: 50%;
    transform: translateY(-50%);
  }

  &:after {
    bottom: 0;
  }

  &__line {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 2px;
    border-radius: 10px;
    background-color: var(--white);
  }

}

.burger--active .burger__line {
    visibility: hidden;
    opacity: 0;
}

.burger--active:before,
.burger--active:after {
  width: 32px;
  background-color: #B3B3B3;
  transform-origin: center;
}

.burger--active:before {
  transform: rotate(45deg);
}

.burger--active:after {
  top: 50%;
  transform: translateY(-50%);
  transform: rotate(-45deg);
}

.burger--active {
  width: 32px;
  height: 32px;
  z-index: 10000;
  position: absolute;
  right: 20px;
}

.burger-wrapper {
  width: 32px;
  height: 32px;
}
