.info-company {
  margin-bottom: 160px;

  @include for-desktop {
    margin-bottom: 120px;
  }

  @include tablet {
    margin-bottom: 70px;
  }

  &__inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 20px;

    @include for-desktop {
      flex-wrap: wrap;
    }
  }

  &__cell {
    width: 49%;

    @include for-desktop {
      width: 100%;
    }

    &:not(:last-child) {

      @include for-desktop {
        margin-bottom: 24px;
      }
    }

  }

  &__list {
  }

  &__item {
    color: var(--bl);
    font-weight: 300;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__head {
    font-family: var(--font-Inter);
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 16px;
  }

  &__address {
  }

  &__work-time {
  }

  &__link-box {
    margin-bottom: 8px;
  }

  &__link-box--end {
    margin-bottom: 0;
  }

  &__link {
    color: var(--bl);
    font-weight: 300;

    &:hover {
      text-decoration: underline;
    }
  }

  &__phone {
  }

  &__mail {
  }

  &__mail-descr {
  }
}

.legal-information {

  &__list {
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
      gap: 5px;
    }

    &:not(:last-child) {
      margin-bottom: 8px;

      @include tablet {
        margin-bottom: 30px;
      }
    }
  }

  &__head {
    color: var(--2-grey);
  }

  &__descr {
    font-weight: 300;
    text-align: right;

    @include tablet {
      text-align: left;
    }
  }
}
