.pagination-site {
  padding: 48px 0 75px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  @include for-desktop {
    padding: 40px 0 55px;
  }

  @include tablet {
    padding: 38px 0 48px;
  }

  &__nav {
    margin-bottom: 64px;

    @include for-desktop {
      margin-bottom: 40px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__item {
    margin-right: 5px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    position: relative;
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    padding-right: 10px;

    &[href]:hover {
      text-decoration: underline;
    }

    &:after {
      content: '/';
      position: absolute;
      top: 0;
      right: 0;
      font-size: inherit;
    }

  }

  &__link--current-page {
    padding-right: 0;
    color: var(--orange);

    &:after {
      content: none;
    }

    &[disabled]:hover {
      text-decoration: none;
    }

  }

  &__title {
    color: var(--white);
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 3.2px;

    @include for-desktop {
      font-size: 40px;
    }
  }

}
