.personal-area {
  padding: 80px 0 160px;

  @include small-tablet {
    padding: 60px 0 100px;
  }

  &__container {
    display: flex;
    gap: 50px;

    @include for-desktop {
      gap: 10px;
    }

    @include tablet {
      flex-direction: column;
      gap: 30px;
    }
  }

  &__subtitle {
    margin-bottom: 48px;
    font-family: var(--font-Bebas);
    font-size: 40px;
    font-weight: 400;

    @include for-desktop {
      font-size: 30px;
      margin-bottom: 35px;
    }

    @include tablet {
      font-size: 25px;
      margin-bottom: 20px;
    }
  }

  &__form {
  }

  &__form-btn {
  }

  &__tabs {
  }

  &__btn {
  }

  &__btn-span {
  }

  &__details {
  }

  &__image {
  }

  &__heading {
    font-size: 16px;
    font-weight: 300;
    font-family: var(--font-Inter);

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__price-row {
  }

  &__price {
    font-size: 20px;
    font-weight: 500;

    @include small-tablet {
      font-size: 18px;
    }
  }

  &__price-old {
    display: block;
    color: var(--light-grey);
    text-decoration: line-through;
  }
}

.table--history {
  width: 860px;

  @include small-tablet {
    font-size: 14px;
  }
}

.tabs {

  &__content-row {
    overflow-x: auto;
  }

  &__btn-row {
    flex: 0 0 25%;
  }

  &__heading {
    display: block;
    font-size: 24px;
    font-weight: 500;
    padding-bottom: 16px;
    margin-bottom: 32px;
    border-bottom: 1px solid var(--3-grey);

    @include small-tablet {
      font-size: 20px;
      margin-bottom: 20px;
      padding-bottom: 5px;
    }
  }

  &__nav-btn--personal {
    background-image: none;

    @include for-desktop {
      padding: 16px;
      font-size: 16px;
    }
  }

  &__exit {
  }

  &__content-row {
  }

  &__content {
  }

  &__panel {
  }

  &__info {
  }
}

.registration-stage {

  &--personal {
    margin-bottom: 40px;
  }

  // &__label--person {
  //   width: 49%;
  // }

  &__label--name-the-site {
    grid-area: 4 / 1 / 5 / 9;

    @include mobile {
      grid-area: 5 / 1 / 5 / 5;
    }
  }

  // &__label--street {
  //   width: 49%;
  // }

  // &__label--phone {
  //   width: 49%;
  // }

  &__label--new-password {
    width: 100%;
  }

  &__label--old-password {
    width: 100%;
  }

  &__label--repeat-old-password {
    width: 100%;
  }
}

.card {

  &__header {
  }

  &__status {
  }

  &__status--sold-out {
  }

  &__moodel {
  }

  &__model-name {
  }

  &__model-prices {
  }

  &__btn {
  }

  &__btn--track {
  }

  &__btn-text {
  }

  &__btn-text--track {
  }
}

.wrapper-grid--personal .card {
  grid-column: 6 span;

  @include for-desktop {
    grid-column: 1 span;
  }
}

.history-orders {
  display: flex;
  align-items: center;

  &__th {
    width: 25%;
    text-align: left;
    color: var(--white);
    font-weight: 400;
    padding: 0 10px;
  }

  &__th:first-child {
    padding-left: 0;
  }

  &__th:last-child {
    padding-right: 0;
  }
}

.thead {
  display: block;
  border-radius: 10px;
  background-color: var(--orange);
  padding: 24px;
  padding-right: 50px;

  @include small-tablet {
    padding: 14px;
    padding-right: 40px;
  }
}

.order {
  display: block;
}

.order-details {
  position: relative;
  display: flex;
  align-items: center;
  padding: 10px 24px;
  padding-right: 50px;
  cursor: pointer;
  border-radius: 10px;
  background-color: var(--bg);

  @include small-tablet {
    padding: 4px 14px;
    padding-right: 40px;
  }

  &::after {
    content: '';
    position: absolute;
    right: 20px;
    width: 15px;
    height: 22px;
    background-image: url('/img/personal-area/arrow.svg');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }

  &__th {
    width: 25%;
    text-align: left;
    font-weight: 400;
    padding: 10px;
  }

  &__th:first-child {
    padding-left: 0;
  }

  &__th:last-child {
    padding-right: 0;
  }
}

.more-details {
  display: flex;
  align-items: center;
  padding: 10px 24px;
  border-bottom: 1px solid var(--3-grey);

  @include small-tablet {
    padding: 0 14px;
  }

  &__td {
    width: 25%;
    padding: 10px;
    text-align: left;
  }

  &__td:first-child {
    padding-left: 0;
  }

  &__td:last-child {
    padding-right: 0;
  }

  &__td--title {
    width: 50%;
  }

  &__td--hidden {
    width: 0%;
    padding: 0;
  }
}

