.blog-grid {
  padding: 48px 0 160px;

  @include tablet {
    padding: 48px 0 100px;
  }

  &__inner {
  }

  &__visible {
    margin-bottom: 40px;
  }

  &__hidden {
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;

  @include for-desktop {
    grid-template-columns: repeat(2, 1fr);
  }

  @include tablet {
    grid-template-columns: repeat(1, 1fr);
  }
}

.card-reviews {
  grid-column: 1 span;

  &__image {

    @include for-desktop {
      width: 100%;
    }
  }
}
