.presentation {

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 79px;

    @include for-desktop {
      flex-direction: column;
    }
  }

  &__subtitle {
    display: inline;
    margin-right: 10px;

    @include for-desktop {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__description {
    padding-top: 6px;
  }

  &__text {
    width: 100%;
    max-width: 510px;
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    margin-bottom: 30px;
    letter-spacing: 0px;

    @include for-desktop {
      max-width: none;
    }
  }

  &__more-link {
    color: var(--light-grey);
    font-family: var(--font-inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    letter-spacing: 0px;
  }

  &__bg {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 600px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;

    @include mobile {
      min-height: 400px;
    }
  }

  &__img {
    transition: transform 0.2s ease-in;

    @include mobile {
      width: 70px;
      height: 70px;
    }
  }

  &__image-play {
    cursor: pointer;
    margin-top: 17px;

    &:hover .presentation__img {
      transform: scale(1.05);
    }
  }

  &__footer {
    background: var(--dark-grey);
    padding: 36px 0px 36px;
  }

  &__list {
    color: var(--white);
    display: flex;

    @include big-desktop {
      flex-wrap: wrap;
    }
  }

  &__item {
    width: min-content;
    margin-right: 47px;

    &:last-child {
      margin-right: 0px;
    }

    &--guarantee {
      margin-right: 90px;
    }

    &--setting {
      margin-right: 45px;
    }

    @include xxx-desktop {
      width: 25%;
      margin-right: 0;
      padding: 0 20px;
    }

    @include big-desktop {
      width: 50%;
      padding: 25px;
    }

    @include tablet {
      width: 100%;
      padding: 25px 0;
    }

  }

  &__head {
    white-space: nowrap;
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    padding-bottom: 8px;
    padding-left: 56px;
    margin-bottom: 17px;
    background-repeat: no-repeat;
    background-size: 40px 40px;
    background-position: 0 0;

    @include xxx-desktop {
      white-space: normal;
    }

    @include big-desktop {
      margin-bottom: 10px;
    }

    @include mobile {
      font-size: 19px;
    }

  }

  &__info {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    letter-spacing: 0px;
  }
}
