.popup {
  position: static;
  z-index: 1111;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  overflow-y: auto;
  overflow-x: hidden;
  // -webkit-transition: 0.5s;
  // -o-transition: 0.5s;
  // transition: 0.5s;

  @include big-desktop {
    position: fixed;
    opacity: 0;
    visibility: hidden;
  }
}

.popup--filter {
  flex: 0 0 290px;
  width: auto;

  @include big-desktop {
    flex: 0 1 auto;
    width: 100%;
  }
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .filter {
  flex: 0 1 100%;
}

.popup__body {
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0px;
  transition-property: background-color, backdrop-filter;
  transition-duration:  0.3s, 0.3s;
  transition-timing-function: ease-in;
  transition-delay: 0s, 0s;
}

.popup.open .popup__body {
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  padding: 50px;

  @include mobile {
    padding: 20px;
  }
}

.popup__content {
  border-radius: 10px;
  color: #000000;
  width: 100%;
  max-width: 600px;
  padding: 0px;
  position: relative;
  opacity: 1;
  transition-delay: 0.4s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.popup.open .popup__content {
  background-color: #fff;
  padding: 50px;
  padding-top: 80px;
  margin: auto;

  @include mobile {
    padding: 20px;
    padding-top: 70px;
  }
}

.popup__close {
  display: none;
  position: absolute;
  z-index: 99;
  top: 12px;
  right: 25px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  padding: 15px;
  background-color: transparent;
}

.popup.open .popup__close {
  display: block;
}

.popup__close:before,
.popup__close:after {
  content: '';
  width: 24px;
  height: 3px;
  background-color: #454545;
  position: absolute;
  top: 14px;
  right: 4px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.popup__close:before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.popup__close:hover:before {
  -webkit-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
  transform: rotate(135deg);
}

.popup__close:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.popup__close:hover:after {
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

.popup__close:hover:before,
.popup__close:hover:after {
  background-color: #ff0202;
}
