.tabs {
  display: block;

  &__nav {
    display: block;
  }

  &__nav-item {

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__nav-item--menu {

    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  &__nav-link {
    width: 100%;
    max-width: 320px;
    color: var(--orange);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    padding: 16px;
    padding-right: 50px;
    border-radius: 10px;
    background-color: var(--bg);
    text-align: left;

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__nav-link--menu {

    @include small-tablet {
      font-size: 16px;
    }

  }

  & .nav__link {
    color: var(--1-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
  }

  &__nav-btn {
    display: block;
    width: 100%;
    background-color: var(--bg);
    background-image: url('../img/svg-icon/arrow-tabs.svg');
    background-repeat: no-repeat;
    background-size: 16px 20px;
    background-position: 95% 50%;
    padding: 24px 32px;
    padding-right: 66px;
    border-radius: 10px;
    font-family: var(--font-Inter);
    font-size: 18px;
    font-weight: 500;
    color: var(--dark);
    text-align: left;
    transition: background-color 0.2s, color 0.2s;

    &:focus {
      color: var(--white);
      background-color: var(--orange);
    }

    &:focus-visible {
      background-color: var(--orange);
    }

    @include small-tablet {
        font-size: 16px;
      }

    @include mobile {
      font-size: 15px;
      padding: 14px 22px;
      padding-right: 40px;
    }
  }

  &__nav-btn:hover {
    color: var(--white);
    background-color: var(--orange);
  }

  &__nav-btn--active {
    color: var(--white);
    background-color: var(--orange);
    background-image: url('../img/svg-icon/arrow-tabs-white.svg'), url('../img/svg-icon/tabs-rotate.svg');
    margin-bottom: 24px;

    @include small-tablet {
      margin-bottom: 18px;
    }
  }

  &__nav-btn--menu {
    padding: 16px;
    padding-right: 25px;
  }

  &__nav-btn--filter {
    padding: 16px;
    padding-right: 25px;
    background-color: transparent;

    &.tabs__nav-btn:hover,
    &.tabs__nav-btn--active {
      color: inherit;
      background-color: transparent;
    }

    &.tabs__nav-btn--active {
      background-image: url('../img/svg-icon/tabs-rotate.svg');
    }
  }

  &__panel {
    color: var(--bl);
    padding: 10px 24px 25px 24px;

    @include mobile {
      padding: 14px;
      padding-top: 5px;
      font-size: 15px;
    }

    &--personal-area {

      @include tablet {
        padding: 0;
      }
    }


    & .view-link {
      display: inline;
      text-align: left;
    }
  }

  &__panel--active {
    display: block;
  }

  &__panel--active.tabs__panel--color {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 13px;

    & .custom-checkbox {
      display: flex;
      margin-bottom: 0;
    }
  }

  &__panel--menu {
    padding: 0 16px 24px;
  }

  &__panel--filter {
    padding: 0;
  }

  &__panel--additional-services {
    padding: 0;
    margin-top: -24px;
  }

}

.tabs-menu {
  margin-bottom: 74px;
  max-width: 320px;

  @include small-tablet {
    margin-bottom: 50px;
  }
}
