.photogallery {
  padding: 80px 0 160px;

  @include for-desktop {
    padding: 60px 0 100px;
  }

  @include tablet {
    padding: 40px 0 100px;
  }

  @include mobile {
    padding: 40px 15px 100px;
  }

  &__subtitle {
    margin-bottom: 80px;

    @include for-desktop {
      font-size: 50px;
      margin-bottom: 60px;
    }

    @include tablet {
      font-size: 40px;
      margin-bottom: 30px;
    }

    @include small-tablet {
      font-size: 32px;
    }
  }

  &__image {
    border-radius: 10px;
    overflow: hidden;
  }
}

.swiper-slide--photogallery {
  max-width: 400px;
}
