.form {
  display: flex;
  align-items: stretch;
  margin-bottom: 14px;

  @include mobile {
    flex-direction: column;
  }

  &__label {
    margin-right: 16px;

    @include mobile {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }

  &__input {
    width: 346px;
    display: inline-block;
    padding: 14px 17px;
    border-radius: 5px;
    background-color: var(--white);

    @include for-desktop {
      width: 300px;
    }

    @include tablet {
      width: 250px;
      padding: 15px;
    }

    @include small-tablet {
      width: 100%;
    }

    &::-webkit-input-placeholder {
      color: var(--light-grey);
      font-family: var(--font-Inter);
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 160%;

      @include tablet {
        font-size: 13px;
        line-height: 140%;
      }
    }

  }

  &__btn {
    padding: 14px 38px;
    border-radius: 5px;

    @include tablet {
      padding: 14px 20px;
      min-width: 160px;
    }

    @include mobile {
      width: 100%;
    }
  }
}
