.contacts {
  padding: 45px 0 130px;

  @include big-desktop {
    padding: 45px 0 70px;
  }

  &__subtitle {
    margin-bottom: 62px;

    @include big-desktop {
      margin-bottom: 30px;
    }
  }

  &__map {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
    min-height: 500px;
    margin-bottom: 61px;

    @include for-desktop {
      min-height: 400px;
    }

    @include tablet {
        margin-bottom: 30px;
    }
  }

  &__image {
    margin-top: 100px;
    margin-left: -65px;

	@include for-desktop {
		width: 80px;
	}

	@include small-tablet {
		width: 60px;
    margin-top: 117px;
		margin-left: -35px;
	}
  }
}


.contacts-list {
  display: flex;

  @include big-desktop {
    justify-content: space-between;
  }

  @include tablet {
    flex-wrap: wrap;
    justify-content: center;
    gap: 32px;
  }

  @include mobile {
    justify-content: space-around;
  }
}

.contacts-item {
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-position: 0 0;
  margin-right: 140px;
  padding-left: 48px;

  @include big-desktop {
    margin-right: 10px;
  }

  @include for-desktop {
    background-size: 32px 32px;
    padding-left: 38px;
  }

  @include tablet {
    width: 35%;
    padding-left: 0;
    padding-top: 40px;
    background-size: 32px 32px;
    background-position: top left;
    margin-right: 0;
  }

  @include mobile {
    width: 45%;
  }

  &:last-child {
    margin-right: 0;
    padding-left: 47px;

	@include for-desktop {
		padding-left: 38px;
	  }

	@include tablet {
		padding-left: 0;
	}

  }

}

.contacts-phone {
  display: block;
  color: var(--bl);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  margin-bottom: 9px;
  letter-spacing: 0px;

  @include for-desktop {
	  margin-bottom: 3px;
  }

  &:last-child {
    margin-right: 0;
  }
}

.contacts-address {
  color: var(--bl);
  font-family: var(--font-Inter);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: -0.07px;
}

.contacts-mail {
  display: block;
  color: var(--bl);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 194%;
  letter-spacing: -0.07px;

  @include mobile {
    font-size: 15px;
  }
}

.contacts-works {
  display: block;
  margin-bottom: 10px;
  letter-spacing: -0.5px;

  @include for-desktop {
	  margin-bottom: 3px;
  }
}

.contacts-time {
  color: var(--bl);
  font-family: var(--font-Inter);
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: -0.25px;
}
