.filter {
  flex: 0 0 290px;

  &__type {
    padding: 16px 7px;
    border-bottom: 1px solid var(--3-grey);

    &:first-child {
      padding-top: 0;
      padding-bottom: 24px;
    }

  }

  &__btn-reset {
    width: 100%;
    margin-top: 32px;
    padding: 16px 60px;
    font-weight: 500;
    border-radius: 10px;
    border: 1px solid var(--3-grey);
  }
}
