.table {
  display: block;
  border: 1px solid var(--bg);
  border-collapse: collapse;
  border-radius: 10px;

  &--additional-services {
    border-radius: 0;
    border: none;
  }

  &__body {
	display: block;
  }

  &__row {
	display: flex;
	align-items: center;

    &:nth-child(even) {
      background-color: var(--bg);
    }

    &--additional-services:nth-child(even) {
      background-color: transparent;
    }

    &--additional-services:not(:last-child) {
      border-bottom: 1px solid var(--3-grey);
    }
  }

  &__td {
    padding: 28px 24px;
	  flex: 1 1 50%;
  }

  &__td--name {
    font-weight: 500;
  }

  &__td--description {
    color: var(--1-grey);
    text-align: right;
    font-family: var(--font-Inter);
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    letter-spacing: 0.18px;
  }
}

.table--basik {

  @include small-tablet {
      font-size: 15px;
    }

  @include mobile {
      font-size: 14px;
    }
}

.table--basik .table__td {

  @include small-tablet {
      padding: 20px 16px;
    }
}

.table--basik .table__td--description {

  @include small-tablet {
      font-size: 16px;
    }
}

.table--additional-services {

  @include small-tablet {
    font-size: 14px;
  }
}

.table--additional-services .table__td {

  @include small-tablet {
    padding: 20px;
  }
}
