.bicycle-workshop {
  padding: 64px 0;
  background: var(--dark-grey);
  color: var(--white);

  &__inner {
    display: flex;
    gap: 40px;

    @include for-desktop {
      flex-direction: column;
      gap: 26px;
    }
  }

  &__box {
    display: flex;
    flex-direction: column;
    gap: 26px;
    width: 50%;

    @include for-desktop {
      width: 100%;
    }
  }
}

.basic-services {
  padding: 64px 0 32px;

		&__title {
      font-size: 40px;
      margin-bottom: 48px;

      @include tablet {
        font-size: 30px;
        margin-bottom: 30px;
      }

      @include small-tablet {
        font-size: 25px;
        margin-bottom: 25px;
      }
		}
}

.additional-services {
  padding: 32px 0 64px;

  &__title {
    font-size: 40px;
    margin-bottom: 48px;

    @include tablet {
      font-size: 30px;
      margin-bottom: 30px;
    }

    @include small-tablet {
      font-size: 25px;
      margin-bottom: 25px;
    }
  }
}
