.brand-warranty {

  &__subtitle {
    margin-bottom: 48px;

    @include xxx-desktop {
      font-size: 35px;
      margin-bottom: 30px;
    }
  }

  &__tanle-inner {
    display: flex;
    justify-content: space-between;
    gap: 40px;

    @include for-desktop {
      flex-wrap: wrap;
      gap: 0;
    }
  }
}
.site-container {
}
.table--guarantees {
  width: 100%;
}

.table--guarantees .table__td--description {

  @include small-tablet {
    font-size: 16px;
  }

  @include mobile {
    font-size: 14px;
  }
}
