.catalog {
  padding: 91px 0px 153px;

  &__subtitle {
    font-family: var(--font-Bebas);
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 2.8px;
    margin-bottom: 61px;

    @include tablet {
      margin-bottom: 30px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 16px;
    grid-template-areas:
      "bicycles bicycles bicycles"
      "trade-in spare-parts equipment"
      "trade-in accessories bicycle-stands"
    ;

    @include for-desktop {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-template-areas:
      "bicycles bicycles"
      "trade-in trade-in"
      "spare-parts equipment"
      "accessories bicycle-stands"
    ;
    }

    @include small-tablet {
      grid-template-rows: repeat(2, 1fr);
      gap: 8px;
    }
  }

  &__grid-item {
    padding: 10px;
    background-color: var(--bl);
    background-repeat: no-repeat;
    border-radius: 10px;
  }

  &__grid-item--bicycles {
    background-color: var(--orange);
  }

  &__grid-head {
    color: var(--white);
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 3.2px;

    @include big-desktop {
      font-size: 54px;
    }

    @include tablet {
      font-size: 44px;
    }

    @include small-tablet {
      font-size: 28px;
    }

    @include mobile {
      font-size: 24px;
    }

  }
}

.bicycles {
  grid-area: bicycles;
  width: 100%;
  height: 304px;
  padding: 120px 80px 100px;
  background-position: 393px 4px;
  background-size: 875px 699px;

  @include big-desktop {
    background-position: center 4px;
    background-size: 575px 499px;
    padding: 20px;
  }

  @include for-desktop {
    height: 264px;
  }

  @include small-tablet {
    height: 214px;
    padding: 15px;
  }
 }

.trade-in {
  grid-area: trade-in;
  width: 100%;
  padding: 30px;
  padding-top: 74px;
  padding-left: 98px;
  height: 458px;
  background-position: -52px 176px;
  background-size: 503px 503px;

  @include big-desktop {
    padding: 20px;
  }

  @include for-desktop {
    height: 264px;
    background-position: right 56px;
  }

  @include small-tablet {
    height: 214px;
    padding: 15px;
  }

 }

.spare-parts {
  grid-area: spare-parts;
  width: 100%;
  height: 190px;
  padding: 40px;
  padding-top: 63px;
  background-position: 250px 31px;
  background-size: contain;

  @include big-desktop {
    padding: 30px;
    background-position: right 31px;
  }

  @include small-tablet {
    padding: 15px;
  }
}

.equipment-item {
  grid-area: equipment;
  height: 239px;
  width: 100%;
  padding: 41px;
  padding-top: 34px;
  background-position: 223px 96px;
  background-size: 225px 235px;

  @include big-desktop {
    padding: 20px;
    background-position: right 96px;
  }

  @include small-tablet {
    padding: 15px;
  }
}

.accessories {
  grid-area: accessories;
  height: 254px;
  margin-top: -113px;
  padding: 40px;
  padding-top: 160px;
  background-position: 99px -146px;
  background-size: auto;

  @include big-desktop {
    padding: 20px;
    background-position: right -38px;
  }

  @include for-desktop {
    margin-top: -71px;
  }

  @include small-tablet {
    margin-top: -50px;
    padding: 15px;
  }
}

.bicycle-stands {
  grid-area: bicycle-stands;
  height: 203px;
  margin-top: -65px;
  padding: 39px;
  padding-top: 35px;
  background-position: 276px -25px;
  background-size: 288px 288px;

  @include big-desktop {
    padding: 20px;
    background-position: center top;
  }

  @include for-desktop {
    margin-top: -20px;
  }

  @include small-tablet {
    margin-top: 1px;
    padding: 15px;
  }
}
