/* stylelint-disable */

@mixin xxx-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin for-desktop {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (426px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (376px)) {
    @content;
  }
}
