// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-Inter: "Inter", sans-serif;
	--font-OpenSans: "Open Sans", sans-serif;
	--font-Bebas: "Bebas Neue", sans-serif;
	--font-BebasCyrillic: "Bebas Neue Cyrillic", sans-serif;
	--font-Montserrat: "Montserrat", sans-serif;
  --content-width: 1310px; // Ширина малого контейнера
  --content-fluid-width: 1890px; //Ширина большого контейнера
  --container-offset: 15px; //padding лево/право
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
  --container-rigth: calc( (100% - var(--content-width))/2 + var(--container-offset));

  // colors
  --bl: #101010;
	--banner: #131313;
	--dark: #34312D;
	--dark-grey: #2E2E2E;
	--1-grey: #4C4C4C;
	--2-grey: #777777;
	--light-grey: #B3B3B3;
	--3-grey: #E5E5E5;
	--bg: #F8F8F8;
	--white: #FFFFFF;
	--orange: #F57520;
	--hover-orange: #F57F2A;
	--or-2: #EB6B16;
	--or-3: #E1610C;
	--or-4: #D75702;
	--red: #F53A20;
	--green: #4D932C;
}
