.documents-guarantee {
  padding: 80px 0;

  @include for-desktop {
    padding: 50px 0;
  }

  &__subtitle {
    font-size: 24px;
    margin-bottom: 48px;
    font-family: var(--font-Inter);

    @include tablet {
      margin-bottom: 20px;
    }

    @include small-tablet {
      font-size: 22px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__list {
    margin-bottom: 32px;
  }

  &__item {
    padding-left: 31px;
    background-repeat: no-repeat;
    background-size: 15px 19px;
    background-position: left center;

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      background-size: 12px 14px;
      padding-left: 21px;
      font-size: 14px;
    }

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__text {
    padding: 24px;
    border-radius: 10px;
    background: var(--bg);
    line-height: 200%;

    @include small-tablet {
      font-size: 14px;
      padding: 20px;
      line-height: 160%;
    }
  }
}
