.footer {
  background-color: var(--bl);

  &__top {
    background-color: var(--banner);
  }

  & .container-fluid {
    background-repeat: no-repeat;
    background-position: 983px top;
    background-size: contain;
    padding: 35px 0px;

    @include xxx-desktop {
      background-position: 625px top;
    }

    @include for-desktop {
      background-position: 380px top;
      background-size: auto;
    }

    @include tablet {
      background-position: 231px top;
    }
  }

  &__top-subtitle {
    color: var(--white);
    font-family: var(--font-Bebas);
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 123%;
    letter-spacing: 3.2px;
    margin-bottom: 37px;

    @include for-desktop {
      font-size: 44px;
      margin-bottom: 17px;
    }

    @include tablet {
      font-size: 40px;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    padding-top: 93px;
    padding-bottom: 40px;
    color: var(--white);

    @include small-tablet {
      flex-direction: column;
      padding-top: 60px;
    }
  }

  &__column {
    width: 100%;
    max-width: 219px;
    margin-right: 10px;
    padding-top: 13px;

    &:first-child {
      max-width: 350px;
      padding-top: 0;
      margin-left: -1px;

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 40px;
      }
    }

    &:last-child {
      margin-right: 0;
    }

  }

  &__column--catalog {
    @include for-desktop {
      display: none;
    }
  }

  &__column--clients {
    @include for-desktop {
      display: none;
    }
  }

  &__logo {
    margin-bottom: 58px;

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  &__logo-description {
    color: var(--light-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    letter-spacing: 0px;
  }

  &__head {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.24px;
    margin-bottom: 32px;
  }

  &__bottom {
    border-top: 1px solid #34312D;
    padding: 12px 0 23px;
  }

  &__copyright {
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    text-transform: uppercase;
    letter-spacing: 0px;
    margin-right: 19px;
  }

  &__policy {
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0px;
  }
}

.footer .nav {

  &__list {
    flex-direction: column;
    align-items: flex-start;
  }

  &__item {
    margin-right: 0;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    font-size: 16px;
    line-height: 160%;
    font-weight: 300;
    text-transform: none;
  }
}

.footer .contacts-list {
  flex-direction: column;
  gap: 0;
}

.footer .footer-contacts {
  padding-top: 0;
  width: 100%;
}

.footer .contacts-item {
  margin-right: 0;
  margin-bottom: 16px;
  background-position: 0 center;

  &:last-child {
    margin-bottom: 0;
  }

  @include for-desktop {
    width: 100%;
    padding-top: 0;
    padding-left: 50px;
    margin-bottom: 25px;
  }

}

.footer .contacts-phone,
.footer .contacts-address,
.footer .contacts-mail {
  color: var(--white);
  display: inline-block;
}

.footer .contacts-phone {
  margin-bottom: 0;
}

.footer .contacts-phone:hover,
.footer .contacts-mail:hover {
  color: var(--orange);
}
