.custom-checkbox {
  position: relative;
  width: auto;
  display: flex;
  margin-bottom: 19px;
	font-family: sans-serif;

  &--brand,
  &--material {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  & .custom-checkbox__content::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: var(--white);
    border: 1px solid var(--2-grey);
    width: 24px;
    height: 24px;
    border-radius: 3px;
  }

  & .custom-checkbox__content::after {
    content: "";
    position: absolute;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
    width: 14px;
    height: 11px;
    background-image: url(../img/svg-icon/checkbox-checked.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    opacity: 0;
  }

  & input:checked + .custom-checkbox__content::before {
    background-color: var(--orange);
    border: 1px solid transparent;
  }

  & input:checked + .custom-checkbox__content::after {
    opacity: 1;
  }

  &__color {
    position: relative;
    grid-column: 2 span;
    display: inline-block;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    cursor: pointer;

    &:focus {
      border: 1px solid red;
    }

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      border: 5px solid transparent;
      border-radius: 50%;
    }

  }

  & input:checked + .custom-checkbox__color::before {
    border: 5px solid var(--3-grey);
  }

  & input:checked + .custom-checkbox__size::before {
    background-color: var(--white);
  }

  & input:checked + .custom-checkbox__size::after {
    background-color: var(--bl);
    background-image: none;
    width: 12px;
    height: 12px;
    left: 6px;
  }


  &__content {
    display: inline-block;
    padding-left: 34px;
    cursor: pointer;
    line-height: 160%;
    letter-spacing: 0;
    font-family: var(--font-Inter);
    color: var(--2-grey);
  }

  &--only-stock {
    width: 100%;
    margin-bottom: 0;

    & .custom-checkbox__content {
      padding-left: 0;
      padding-right: 34px;
    }

    & .custom-checkbox__content::before {
      left: auto;
      right: 0;
      width: 40px;
      height: 21px;
      background-color: var(--white);
      border: 1px solid var(--2-grey);
      border-radius: 22px;
      transition-property:  background-color, border;
      transition-duration:  0.1s,  0.1s;
      transition-timing-function: ease-in;
      transition-delay: 0s, 0s;
    }

    & .custom-checkbox__content::after {
      left: auto;
      right: 20px;
      background-image: none;
      background-color: var(--orange);
      width: 17px;
      height: 17px;
      border-radius: 50%;
      opacity: 1;
      transition-property:  background-color, right;
      transition-duration:  0.1s,  0.1s;
      transition-timing-function: ease-in;
      transition-delay: 0s, 0s;
    }

    & input:checked + .custom-checkbox__content::after {
      background-color: var(--white);
      right: 3px;
    }

    & input:checked + .custom-checkbox__content::before {
      background-color: var(--orange);
      border: 1px solid transparent;
    }

  }

}

.custom-checkbox--policy {

  & .custom-checkbox__content::before {
    border: none;
  }

  & .custom-checkbox__content::after {
    width: 15px;
    height: 15px;
    background-color: var(--bl);
    border-radius: 3px;
  }

  & .custom-checkbox__content {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    padding-left: 35px;
    letter-spacing: 0px;
    vertical-align: text-bottom;
  }

  & input:checked + .custom-checkbox__content::before {
    background-color: var(--white);
  }

  & input:checked + .custom-checkbox__content::after {
    background-color: var(--bl);
    background-image: none;
    width: 12px;
    height: 12px;
    left: 6px;
  }

}
