.information-delivery {
  padding: 48px 0px 160px;

  @include for-desktop {
    padding: 40px 0 100px;
  }

  &__inner {

    &:not(:last-child) {
      margin-bottom: 80px;

      @include for-desktop {
        margin-bottom: 50px;
      }
    }
  }

  &__info {
    width: 100%;
    max-width: 904px;
    margin-bottom: 40px;
  }

  &__subtitle {
    font-size: 40px;
    margin-bottom: 32px;

    @include for-desktop {
      font-size: 30px;
      margin-bottom: 20px;
    }
  }

  &__text {

    &:not(:last-child) {
      margin-bottom: 24px;

      @include for-desktop {
        margin-bottom: 15px;
      }
    }
  }

  &__phone {
    color: var(--orange);
  }

  &__tabs {
  }

  &__list {
  }

  &__item {
    color: var(--1-grey);

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__tabs-text {
    color: var(--1-grey);
  }
}
