.header {

    &--row {
      background-color: var(--bl);
      padding: 28px 0;
    }

    &__container {
        position: relative;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center top;
        color: var(--white);
        min-height: 875px;
        padding-top: 27px;

        @include big-desktop {
          min-height: 768px;
        }

        @include small-tablet {
          min-height: 700px;
        }
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__box {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        margin-right: 20px;
    }

    &__nav {
        margin-right: 76px;

        @include big-desktop {
          margin-right: 40px;
        }

        @include for-desktop {
          display: none;
        }
    }

    &__user-row {
      margin-right: 57px;

      @include big-desktop {
        margin-right: 40px;
      }

      @include mobile {
        margin-right: 30px;
      }
  }
}

.nav {

    &__list {
        display: flex;
        align-items: center;
    }

    &__item {
        margin-right: 32px;

        &:last-child {
            margin-right: 0;
        }

        @include big-desktop {
          margin-right: 25px;
        }
    }

    &__link {
        color: var(--white);
        font-size: 12px;
        letter-spacing: 0px;
        text-transform: uppercase;
        transition: color 0.2s ease-in-out;

        &:hover {
            color: var(--orange);
        }

        &--active {
            color: var(--orange);
        }
    }
}

.hero {

    &__info {
        max-width: 600px;
        padding-top: 97px;
    }

    &__subtitle {
        font-size: 110px;
        margin-bottom: 19px;
        font-weight: 400;
        line-height: 123%;
        letter-spacing: 2px;
        font-family: var(--font-Bebas);
        color: var(--white);

        @include big-desktop {
          font-size: 90px;
          line-height: 110%;
        }

        @include tablet {
          font-size: 70px;
        }

        @include mobile {
          font-size: 60px;
        }
    }

    &__text {
        font-size: 18px;
        font-style: normal;
        font-weight: 300;
        line-height: 154%;
        letter-spacing: 0.18px;
        margin-bottom: 42px;
    }

}

.user-icon {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__link {
        margin-right: 24px;
        cursor: pointer;

        @include mobile {
          margin-right: 16px;
        }

        &:last-child {
            margin-right: 0;
        }

        &:hover .svg-header-user {
            stroke: rgb(255, 255, 255, 0.7);
        }
    }

}

.svg-header-user {
    width: 24px;
    height: 24px;
    vertical-align: -webkit-baseline-middle;
    fill: transparent;
    stroke: var(--white);
    transition: stroke 0.2s ease-in-out;
}

.more {
    display: flex;
    align-items: stretch;
    justify-content: center;

    @include small-tablet {
      display: none;
    }

    &__item {
        width: 100%;
        color: var(--white);
        padding-left: 25px;
        font-family: var(--font-Inter);
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 160%;
        letter-spacing: 0.24px;

        @include big-desktop {
          padding-left: 5px;
        }

        @include for-desktop {
          font-size: 18px;
        }

        @include tablet {
          font-size: 14px;
        }
    }

    &__item--extreme {
        background-color: var(--bl);
    }

    &__container {
      padding: 48px;
      padding-left: 23px;

      @include big-desktop {
        padding: 30px;
      }

      @include for-desktop {
        padding: 20px;
      }

      @include tablet {
        padding: 15px;
      }
    }

    &__container--extreme {
      padding-right: 92px;
      width: fit-content;
      margin-left: auto;

      @include big-desktop {
        padding-right: 30px;
      }

      @include tablet {
        padding-right: 15px;
      }
    }

    &__item--professional {
        background-color: var(--banner);
        max-width: 427px;
    }

    &__item--road-bike {
        background-color: var(--dark-grey);
    }

}

.logo .image {
  max-width: none;

  @include mobile {
    width: 67px;
    height: auto;
  }
}
