.delivery {
  padding: 80px 0;

  @include tablet {
    padding: 30px 0;
  }

  &__title {
    font-size: 40px;
    margin-bottom: 40px;

    @include tablet {
      font-size: 30px;
      margin-bottom: 20px;
    }
  }

  &__bg {
    width: 100%;
    height: 186px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    margin-bottom: 24px;
  }

  &__subtitle {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    margin-bottom: 40px;

    @include tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__list {
    padding-left: 17px;
    margin-bottom: 42px;

    @include tablet {
      margin-bottom: 20px;
    }
  }

  &__item {
    color: var(--dark);

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__text {
    margin-bottom: 32px;

    @include tablet {
      margin-bottom: 20px;
    }
  }

  &__text--detail {
    display: inline-block;
    border-radius: 10px;
    padding: 10px 16px;
    background: var(--bg);
  }

  &__link-phone {
    color: var(--orange);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.18px;

    &:hover {
      text-decoration: underline;
    }

    @include mobile {
      font-size: 16px;
    }
  }

  &__head {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.18px;

    @include mobile {
      font-size: 16px;
    }
  }
}
