/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.filter-price__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
}

.filter-price__label {
  display: flex;
  width: 133px;
  height: 40px;
  border-radius: 10px;
  border: 1px solid var(--3-grey);
  padding: 10px;
}

.filter-price__input {
  border: none;
  width: 100%;
}

.filter-price__slider {
  margin: 0 10px;
  margin-bottom: 25px;
}





.noUi-connect {
	background-color: var(--bl);
}

.noUi-target {
	border-radius: 5px;
  background-color: var(--3-grey);
	border: none;
	border-radius: 100px;
	box-shadow: none;
	height: 6px;
}

.noUi-handle::after,
.noUi-handle::before {
	content: none;
}

.noUi-handle {
  border: none;
	box-shadow: none;
	border-radius: 100%;
	background-color: #fff;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: var(--white);
  }
}



.noUi-horizontal .noUi-handle {
  top: -5px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: var(--bl);
}

.noUi-horizontal .noUi-handle-upper {
	right: -1px;
}

.noUi-horizontal .noUi-handle-lower {
  right: -2px;
}
