.have-questions {
  padding: 45px 0 45px;
  background-color: var(--orange);
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: cover;
  color: var(--white);

  @include xxx-desktop {
    background-position: 45% center;
  }

  @include small-tablet {
    background-position: 50% center;
  }

  &__inner {
    width: 100%;
    max-width: 534px;
    margin-right: auto;
  }

  &__title {
    margin-bottom: 32px;
    color: var(--white);

    @include for-desktop {
      font-size: 50px;
      margin-bottom: 10px;
    }

    @include tablet {
      font-size: 40px;
    }
  }

  &__box-phone {
    margin-bottom: 32px;

    @include tablet {
      margin-bottom: 10px;
    }
  }

  &__head {
    display: block;
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 500;

    @include tablet {
      font-size: 18px;
      margin-bottom: 10px;
    }
  }

  &__link {
    font-size: 64px;
    line-height: 1.23;
    font-family: var(--font-Bebas);
    transition: color 0.3s;

    @include for-desktop {
      font-size: 50px;
    }

    @include tablet {
      font-size: 40px;
    }

    &:hover {
      color: var(--3-grey);
    }
  }

  &__text {
    font-weight: 300;
  }
}
