.no-service {
  padding: 40px 0 80px;

  &__subtitle {
    font-size: 24px;
    margin-bottom: 48px;
    font-family: var(--font-Inter);

    @include small-tablet {
      font-size: 22px;
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 18px;
      margin-bottom: 25px;
    }
  }

  &__info {
    width: 100%;
    display: flex;
    gap: 25px;
    align-items: stretch;
    margin-bottom: 25px;

    @include tablet {
      flex-wrap: wrap-reverse;
    }
  }

  &__list {
    width: 70%;
    border-radius: 10px;
    background: var(--orange);
    padding: 40px;
    padding-left: 60px;
    color: var(--white);
    list-style-type: disc;

    @include for-desktop {
      width: 75%;
      padding: 20px;
      padding-left: 40px;
    }

    @include tablet {
      width: 100%;
    }
  }

  &__box-img {

    @include tablet {
      width: 100%;
    }
  }

  &__img {
    display: flex;
    height: 100%;
    max-height: 520px;
  }

  &__image {
    border-radius: 10px;

    @include tablet {
      width: 100%;
      height: 322px;
    }
  }

  &__item {
    line-height: 160%;

    @include for-desktop {
      line-height: 150%;
    }

    @include mobile {
      font-size: 14px;
    }

    &:not(:last-child) {
      margin-bottom: 18px;

      @include for-desktop {
        margin-bottom: 15px;
      }
    }
  }

  &__text {
    padding: 40px;
    border-radius: 10px;
    background-color: var(--bg);

    @include for-desktop {
      padding: 20px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__image {
  }
}

.color-red {
  color: var(--red);
  font-size: 24px;
  font-weight: 500;
}
