.pagination-site {

  &__nav--no-bg {
    margin-bottom: 0;
  }

  &__link--product {
    color: var(--1-grey);
  }
}

.product {
  padding-bottom: 75px;

  @include tablet {
    padding-bottom: 30px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 16px;
    align-items: start;
    grid-template-areas:
      "product__images product__name"
      "product__images product__description"
      "product__images product__description"
    ;
    margin-bottom: 75px;

    @include for-desktop {
      grid-template-areas:
      "product__name product__description"
      "product__images product__description"
      "product__images product__description"
    ;
    column-gap: 70px;
    }

    @include tablet {
      column-gap: 30px;
      margin-bottom: 60px;
    }

    @include small-tablet {
      grid-template-areas:
      "product__name product__name"
      "product__images product__images"
      "product__description product__description"
    ;
    }

  }

  &__images {
    grid-area: product__images;
    width: 100%;
    max-width: 623px;

    @include for-desktop {
      max-width: 400px;
    }

    @include tablet {
      max-width: none;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  &__grid-item--small {
    display: inline-block;
    width: 180px;
    margin-right: 15px;
    opacity: 0.5;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-in;
    transition-delay: 0s;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      opacity: 1;
    }

    @include for-desktop {
      width: 80px;
    }

    @include tablet {
      width: 100px;
    }

    @include small-tablet {
      width: 70px;
    }
  }

  &__grid-item--big {

    @include small-tablet {
      max-width: 420px;
      height: 350px;
      margin: auto;
    }

    @include small-mobile {
      height: 280px;
    }
  }

  &__name {
    grid-area: product__name;
    width: 100%;
    max-width: 510px;
    margin-left: auto;
    margin-bottom: 40px;

    @include for-desktop {
      margin-bottom: 20px;
      margin-left: 0px;
    }

    @include tablet {
      max-width: 300px;
    }

    @include small-tablet {
      max-width: none;
    }
  }

  &__title {
    font-size: 40px;
    margin-bottom: 20px;

    @include for-desktop {
      font-size: 30px;
    }

    @include tablet {
      font-size: 23px;
    }
  }

  &__subtitle {
    font-size: 40px;
    margin-bottom: 40px;

    @include tablet {
      font-size: 30px;
      margin-bottom: 20px;
    }
  }

  &__description-text {
    margin-bottom: 80px;

    @include tablet {
      margin-bottom: 40px;
    }
  }

  &__additional-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &__status {
    color: var(--green);
  }

  &__description {
    grid-area: product__description;
    width: 100%;
    max-width: 510px;
    margin-left: auto;

    @include tablet {
      max-width: 300px;
    }

    @include small-tablet {
      max-width: none;
      margin-left: 0;
    }
  }

  &__price-wrapper {
    margin-bottom: 32px;
  }

  &__price-container {
    margin-bottom: 40px;

    @include for-desktop {
      margin-bottom: 20px;
    }
  }

  &__price {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.4px;

    @include for-desktop {
      font-size: 30px;
    }

    @include tablet {
      font-size: 25px;
    }
  }

  &__price--old {
    color: var(--light-grey);
    font-size: 18px;
    letter-spacing: 0.18px;
    text-decoration-line: line-through;
    margin-left: 16px;

    @include tablet {
      font-size: 16px;
    }
  }

  &__text {
    color: var(--1-grey);

    @include tablet {
      font-size: 15px;
    }
  }

  &__size {
    margin-bottom: 32px;
  }

  &__color {
    margin-bottom: 48px;
  }

  &__head {
    display: inline-block;
    margin-bottom: 24px;
  }

  &__basket {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 16px;

    @include mobile {
      justify-content: flex-end;
    }
  }

  &__favorites-btn {
    flex: 0 0 52px;
    height: 52px;
    border-radius: 10px;
    background-color: #F8F8F8;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 32px 29px;
    cursor: pointer;

    @include mobile {
      order: -1;
    }
  }

  &__btn {

    @include mobile {
      width: 100%;
    }
  }

  // &__characteristics {
  //   margin-bottom: 80px;
  // }

  & .table__row {

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  & .table__td--name {

    @include tablet {
      padding: 15px;
      padding-bottom: 0;
    }
  }

  & .table__td--description {

    @include tablet {
      padding: 15px;
      font-size: 16px;
      text-align: left;
    }
  }
}

.vendor-code {

  &__span {
    display: block;
    margin-bottom: 12px;
    color: var(--light-grey);

    @include tablet {
      font-size: 13px;
      margin-bottom: 8px;
    }
  }
}

.social {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px;

  &__link {
    background-size: 22px 21px;
    background-position: center center;
    background-repeat: no-repeat;
    width: 22px;
    height: 21px;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-in;
    transition-delay: 0s;

    &:hover {
      opacity: 0.6;
    }
  }
}

.size {

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 16px;
  }
}

.custom-checkbox {

  &__size {
    display: inline-block;
    padding: 10px;
    min-width: 56px;
    border-radius: 5px;
    text-align: center;
    border: 1px solid var(--3-grey);
    cursor: pointer;
  }
}

.custom-checkbox--size {
  margin: 0;

  & input:checked + .custom-checkbox__size {
    background-color: var(--bl);
    color: var(--white);
  }
}

.custom-checkbox--color {
  display: flex;
  margin: 0;
}

.color {

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 16px;
  }
}

.items {

  &__control {
    width: 30px;
    cursor: pointer;
    transition: 0.2s ease-in;
    text-align: center;
  }

  &__current {
    min-width: 40px;
    text-align: center;
  }
}

.counter-wrapper {
  border: 1px solid #f2ede7;
  border-radius: 8px;
  min-width: 83px;
  height: 52px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  padding: 10px;
  overflow: hidden;
}
