.burger {
  position: relative;
  width: 32px;
  height: 18px;
  cursor: pointer;
  transition-duration: 0.2s;
  transition-property: opacity;
  transition-delay: 0s;
}
.burger:hover {
  opacity: 0.7;
}
.burger:before, .burger:after {
  content: "";
  position: absolute;
  right: 0;
  width: 24px;
  height: 2px;
  border-radius: 10px;
  background-color: var(--white);
  transition-duration: 0.2s;
  transition-property: all;
  transition-delay: 0s;
}
.burger:before {
  top: 50%;
  transform: translateY(-50%);
}
.burger:after {
  bottom: 0;
}
.burger__line {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  border-radius: 10px;
  background-color: var(--white);
}

.burger--active .burger__line {
  visibility: hidden;
  opacity: 0;
}

.burger--active:before,
.burger--active:after {
  width: 32px;
  background-color: #B3B3B3;
  transform-origin: center;
}

.burger--active:before {
  transform: rotate(45deg);
}

.burger--active:after {
  top: 50%;
  transform: translateY(-50%);
  transform: rotate(-45deg);
}

.burger--active {
  width: 32px;
  height: 32px;
  z-index: 10000;
  position: absolute;
  right: 20px;
}

