  .popup--filter {
    flex: 0 1 auto;
    width: 100%;
  }
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .filter {
  flex: 0 1 100%;
}

.popup__body {
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0px;
  transition-property: background-color, backdrop-filter;
  transition-duration: 0.3s, 0.3s;
  transition-timing-function: ease-in;
  transition-delay: 0s, 0s;
}

.popup.open .popup__body {
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  padding: 50px;
}
@media (max-width: 426px) {
  .popup.open .popup__body {
    padding: 20px;
  }
}

.popup__content {
  border-radius: 10px;
  color: #000000;
  width: 100%;
  max-width: 600px;
  padding: 0px;
  position: relative;
  opacity: 1;
  transition-delay: 0.4s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

