}

.popup__close {
  display: none;
  position: absolute;
  z-index: 99;
  top: 12px;
  right: 25px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  padding: 15px;
  background-color: transparent;
}

