.advantages {
  padding: 95px 0 175px;

  @include for-desktop {
    padding: 80px 0 100px;
  }

  &__subtitle {
    margin-bottom: 63px;

    @include for-desktop {
      margin-bottom: 30px;
    }
  }

  &__inner {
    display: flex;
    align-items: stretch;
  }

  &__bg {
    width: 100%;
    max-width: 821px;
    margin-left: auto;
    height: 100%;
    background-repeat: no-repeat;
    background-size: 821px 574px;
    background-position: center center;
    border-radius: 10px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    align-items: stretch;
    gap: 40px;
    max-width: 620px;
    box-sizing: border-box;
    margin: auto;

    @include for-desktop {
      gap: 20px;
    }

    @include tablet {
      grid-template-columns: repeat(12, 1fr);
      max-width: none;
      gap: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 290px;
    grid-column: 3 span;
    background-color: var(--orange);
    padding: 32px;
    padding-bottom: 29px;
    border-radius: 10px;

    @include for-desktop {
      padding: 20px;
    }

    @include tablet {
      max-width: none;
      padding: 25px;
    }
  }

  &__head {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    margin-bottom: 17px;

    @include for-desktop {
      margin-bottom: 10px;
      line-height: 140%;
      font-size: 17px;
    }
  }

  &__text {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    margin-bottom: 22px;
    letter-spacing: 0px;
    flex: 1 1 auto;

    @include for-desktop {
      font-size: 15px;
      line-height: 140%;
      margin-bottom: 15px;
    }
  }

  &__more {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    letter-spacing: 0.2px;
    transition: color 0.2s ease-in;

    &:hover {
      color: #e1e1e1;
    }

  }

  &__row {
    width: 100%;
    height: 100%;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  & .slider-container {
    overflow: hidden;
  }

}

.row-left {
  width: 100%;
  background-color: #131310;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;

  @include xxx-desktop {
    width: 70%;
  }

  @include for-desktop {
    width: 50%;
  }

  @include tablet {
    display: none;
  }
}

.row-center {
  width: 97%;
  padding: 0 25px;

  @include for-desktop {
    padding: 0 20px;
  }

  @include tablet {
    width: 100%;
  }
}

.row-right {
  width: 37%;

  @include big-desktop {
    display: none;
  }
}
