.for-questions {
  padding: 52px 0 40px;
  background-color: var(--orange);
  background-repeat: no-repeat;
  background-size: 1072px 1072px;
  background-position: center right;

  @include big-desktop {
    background-position: 350px center;
  }

  @include for-desktop {
    background-position: 220px center;
  }

  @include tablet {
    background-position: 80px center;
  }

  @include small-tablet {
    background-size: 0px;
  }

  &__inner {
    width: 100%;
    max-width: 620px;
    padding: 15px;
  }

  &__title {
    color: var(--white);
    margin-bottom: 32px;

    @include big-desktop {
      font-size: 30px;
    }

    @include for-desktop {
      font-size: 22px;
      margin-bottom: 20px;
    }

    @include small-tablet {
      font-size: 26px;
      text-align: center;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__form {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;

    @include small-tablet {
      justify-content: center;
    }
  }

  &__label {
    width: 48.6%;

    @include for-desktop {
      width: 33%;
    }

    @include small-tablet {
      width: 47%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__label--person {
  }

  &__input {
    width: 100%;
    border-radius: 10px;
    background-color: var(--white);
    padding: 14px 16px;

    @include big-desktop {
      padding: 12px;
      font-size: 15px;
    }

    &::-webkit-input-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &:-moz-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &::-o-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &:-ms-input-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &::placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
  }

  &__input--textarea {
    width: 100%;
    min-height: 154px;
    max-height: 250px;
    resize: vertical;
    border: none;
  }

  &__label--mail {
  }

  &__label--phone {
  }

  &__label--company {
  }

  &__label--textarea {
    width: 100%;

    @include for-desktop {
      width: 69%;
    }

    @include small-tablet {
      width: 97%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__btn {
    width: 100%;
    text-align: center;
    color: var(--orange);
    background-color: var(--white);
    border-radius: 10px;
    padding: 17px;

    @include for-desktop {
      width: 69%;
    }

    @include small-tablet {
      width: 97%;
    }

    @include mobile {
      width: 100%;
    }
  }
}
