.placing-order {
  padding: 40px 0 160px;

  @include for-desktop {
    padding: 25px 0 100px;
  }

  &__container {
    display: flex;
    gap: 16px;
    grid-template-columns: repeat(1, 2fr);
    width: 100%;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__title {
    font-size: 40px;
    margin-bottom: 40px;

    @include tablet {
      font-size: 35px;
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 30px;
    }
  }
}

.registration-stage {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;

  &--personal {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 16px;

    @include tablet {
      gap: 10px;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &--password {
    margin-bottom: 30px;
  }

  &__item {
    border-radius: 10px;
    border: 1px solid var(--3-grey);
    padding: 40px;
    width: 100%;

    @include big-desktop {
      padding: 20px;
    }
  }

  &__item--payment {
    max-width: 896px;
  }

  &__item--delivery {
    max-width: 368px;

    @include big-desktop {
      max-width: 305px;
    }

    @include tablet {
      max-width: none;
    }
  }

  &__item--your-order {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    column-gap: 88px;

    @include big-desktop {
      column-gap: 50px;
      flex-wrap: wrap;
    }
  }

  &__subtitle {
    font-weight: 400;
    line-height: 123%;
    margin-bottom: 32px;

    @include big-desktop {
      width: 100%;
      font-size: 30px;
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 25px;
    }
  }

  &__subtitle--you-order {
    margin-bottom: 0;
    margin-right: auto;

    @include big-desktop {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__fieldset-form {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 16px;

    @include tablet {
      gap: 10px;
    }

    @include mobile {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 10px;
    }
  }

  &__fieldset {
    display: flex;
    flex-direction: column;
    gap: 14px;
  }

  & .custom-checkbox {
    margin-bottom: 0;
  }

  &__label--person-name {
    grid-area: 1 / 1 / 2 / 5;

    @include mobile {
      grid-area: 1 / 1 / 2 / 3;
    }
  }

  &__label--person-family {
    grid-area: 1 / 5 / 2 / 9;

    @include mobile {
      grid-area: 1 / 3 / 2 / 5;
    }
  }

  &__label--city {
    grid-area: 2 / 1 / 3 / 4;

    @include mobile {
      grid-area: 2 / 1 / 3 / 3;
    }
  }

  &__label--street {
    grid-area: 2 / 4 / 3 / 7;

    @include mobile {
      grid-area: 2 / 3 / 3 / 5;
    }
  }

  &__label--house {
    grid-area: 2 / 7 / 3 / 8;
    min-width: 73px;

    @include mobile {
      grid-area: 3 / 1 / 4 / 3;
    }
  }

  &__label--apartment {
    grid-area: 2 / 8 / 3 / 9;
    min-width: 73px;

    @include mobile {
      grid-area: 3 / 3 / 4 / 5;
    }
  }

  &__label--phone {
    grid-area: 3 / 1 / 4 / 5;

    @include mobile {
      grid-area: 4 / 1 / 5 / 3;
    }
  }

  &__label--mail {
    grid-area: 3 / 5 / 4 / 9;

    @include mobile {
      grid-area: 4 / 3 / 5 / 5;
    }
  }

  &__label--textarea {
    grid-area: 4 / 1 / 5 / 9;

    @include mobile {
      grid-area: 5 / 1 / 6 / 5;
    }
  }

  &__input {
    width: 100%;
    padding: 16px;
    border-radius: 10px;
    background-color: var(--bg);
    color: var(--light-grey);

    &::-webkit-input-placeholder {
      color: var(--light-grey);
    }
    &:-moz-placeholder {
      color: var(--light-grey);
    }
    &::-o-placeholder {
      color: var(--light-grey);
    }
    &:-ms-input-placeholder {
      color: var(--light-grey);
    }
    &::placeholder {
      color: var(--light-grey);
    }

    @include small-tablet {
      padding: 10px;
      font-size: 14px;
    }

    @include mobile {
      font-size: 13px;
    }
  }

  &__textarea {
    border: none;
    width: 100%;
    min-height: 160px;
    max-height: 400px;
    color: var(--light-grey);
    background-color: var(--bg);
    padding: 16px;
    border-radius: 10px;
    resize: vertical;

    &::-webkit-input-placeholder {
      color: var(--light-grey);
    }
    &:-moz-placeholder {
      color: var(--light-grey);
    }
    &::-o-placeholder {
      color: var(--light-grey);
    }
    &:-ms-input-placeholder {
      color: var(--light-grey);
    }
    &::placeholder {
      color: var(--light-grey);
    }

    @include small-tablet {
      min-height: 100px;
      padding: 10px;
      font-size: 14px;
    }
  }

  &__legend {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.18px;
    margin-bottom: 17px;

    @include tablet {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  &__fieldset--delivery {
    margin-bottom: 24px;

    @include tablet {
      margin-bottom: 0;
    }

    @include small-tablet {
      width: 100%;
      margin-bottom: 24px;
    }
  }

  &__row-total {
    width: 100%;
    max-width: 346px;

    @include for-desktop {
      max-width: 300px;
    }

    @include small-tablet {
      max-width: none;
    }
  }

  &__total-inner {
    display: flex;
    flex-wrap: wrap;
    column-gap: 38px;
    color: var(--bl);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    border-bottom: 1px solid var(--3-grey);
    padding-bottom: 24px;
    margin-bottom: 40px;
  }

  &__btn {
    width: 100%;
    margin-bottom: 10px;
  }

  &__policy {
    color: var(--2-grey);
    font-size: 12px;
    line-height: 160%;
  }

  &__policy-link {
    font-size: 12px;
    color: var(--orange);
  }
}

.custom-checkbox {

  &__delivery {
    position: relative;
    padding-left: 34px;
    color: var(--2-grey);
    cursor: pointer;

    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 50%;
    }

    &:before {
      width: 24px;
      height: 24px;
      border: 1px solid var(--light-grey);
    }
  }

  & input:checked + .custom-checkbox__delivery::before {
    background-color: var(--orange);
    border: 1px solid transparent;
  }

  & input:checked + .custom-checkbox__delivery::after {
    width: 12px;
    height: 12px;
    left: 6px;
    background-color: var(--white);
  }
}

.delivery-type {
  width: 100%;
  max-width: 346px;

  @include small-tablet {
    max-width: none;
    margin-bottom: 24px;
  }

  &__heading {
    display: block;
    color: var(--orange);
    font-family: var(--font-Inter);
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.24px;
    margin-bottom: 24px;

    @include tablet {
      font-size: 20px;
      margin-bottom: 10px;
    }
  }

  &__item {
    margin-bottom: 16px;
  }

  &__head {
    display: block;
    font-weight: 400;
    margin-bottom: 3px;
  }

  &__person-info {
    font-weight: 300;
    color: var(--1-grey);
  }
}

.delivery-container {

  @include tablet {
    display: flex;
    flex-wrap: wrap;
    column-gap: 38px;
  }

}

.delivery-type-container {
  display: flex;
  column-gap: 70px;

  @include tablet {
    column-gap: 50px;
  }

  @include small-tablet {
    flex-wrap: wrap;
  }

}
