html {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-Inter, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-height: 100%;
  background-color: var(--white);
  min-width: 320px;
  font-family: var(--font-Inter);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
  letter-spacing: 0.8px;
  font-style: normal;
  color: var(--bl);
  cursor: default;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

.site-container {
  margin: 0 auto;
  max-width: var(--content-width);
  padding: 0 var(--container-offset);
}

.container-fluid {
  margin: 0 auto;
  max-width: var(--container-fluid-width);
}

.container-right {
  padding-left: var(--container-rigth);
  overflow: hidden;
}

.hover-link {
  transition: color 0.2s ease-in;
}

.hover-link:hover {
  color: var(--orange);
}

.swiper-button-next:after,
.swiper-button-prev:after {
	display: none;
}

.swiper-button-next,
.swiper-button-prev {
	width: 43px;
	height: 43px;
	border-radius: 50%;
	background-color: var(--bl);
	background-image: url(../img/svg-icon/arrow.svg);
	background-repeat: no-repeat;
	background-size: 14px 24px;
	background-position: center center;
	transition: background-color 0.3s ease-in-out;

	&:hover {
		background-color: #333333;
	}
}

.swiper-button-prev.swiper-button-disabled {
  opacity: 1;
  background-color: transparent;
  background-image: url(../img/svg-icon/disabled-arrow.svg);
  transform: rotate(0deg);
}

.swiper-button-next.swiper-button-disabled {
  opacity: 1;
  background-color: transparent;
  background-image: url(../img/svg-icon/disabled-arrow.svg);
  transform: rotate(180deg);
}

.swiper-button-prev {
	transform: rotate(180deg);
}

.swiper-slide {
  height: auto;
  display: flex;
  align-items: stretch;
}

.partner .swiper-slide {
  justify-content: center;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

// User style
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100%;
}

.main {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

body.lock {
  overflow: hidden;
}

h1, h2, h3, h4 {
font-family: var(--font-Bebas);
font-style: normal;
color: var(--bl);
font-weight: 700;
line-height: 1.23;
letter-spacing: 3.2px;
margin: 0;
}

h1 {
font-size: 110px;
letter-spacing: 2.2px;
}

h2 {
font-size: 64px;
font-weight: 400;
letter-spacing: 3.2px;

@include small-tablet {
  font-size: 50px;
  line-height: 1.1;
}

}

h3 {
font-size: 40px;
letter-spacing: 2px;
}

h4 {
font-size: 20px;
}

a {
font-family: var(--font-Inter);
font-size: 16px;
font-weight: 400;
line-height: 1.4;
color: var(--white);
display: inline-block;
text-decoration: none;
}

button {
background-color: transparent;
border: 0;
padding: 0;
letter-spacing: 0.8px;
}

.btn {
width: fit-content;
min-width: 180px;
font-family: var(--font-Inter);
display: flex;
align-items: center;
justify-content: center;
text-align: center;
padding: 16px 41px;
gap: 10px;
font-size: 16px;
line-height: 1.23;
letter-spacing: 0px;
font-weight: 500;
color: var(--white);
background-color: var(--orange);
cursor: pointer;
border-radius: 10px;
-webkit-box-shadow: 0px 2px 4px rgba(75, 75, 75, 0.3);
box-shadow: 0px 2px 4px rgba(75, 75, 75, 0.3);
-webkit-transition: box-shadow 0.3s, background-color 0.3s ease-in-out;
-o-transition: box-shadow 0.3s, background-color 0.3s ease-in-out;
transition: box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn:hover {
-webkit-box-shadow: 0px 4px 8px rgba(75, 75, 75, 0.5);
box-shadow: 0px 4px 8px rgba(75, 75, 75, 0.5);
background-color: var(--hover-orange);
}
