.our-team {
  padding: 80px 0;

  @include tablet {
    padding: 80px 0 40px;
  }

  &__title {
    margin-bottom: 80px;

    @include xxx-desktop {
      font-size: 50px;
      margin-bottom: 30px;
    }

    @include for-desktop {
      font-size: 40px;
      margin-bottom: 25px;
    }

    @include small-tablet {
      font-size: 32px;
    }
  }

  &__inner {
    display: flex;
    align-items: stretch;
    gap: 66px;

    @include for-desktop {
      gap: 25px;
    }

    @include tablet {
      flex-wrap: wrap-reverse;
    }
  }

  &__cell {
  }

  &__cell--info {
    width: 53%;

    @include tablet {
      width: 100%;
    }
  }

  &__text {
    margin-bottom: 64px;

    @include big-desktop {
      margin-bottom: 40px;
    }

    @include for-desktop {
      font-size: 15px;
      margin-bottom: 20px;
    }
  }

  &__image {
    height: 100%;
    max-height: 600px;
    border-radius: 10px;
    overflow: hidden;
  }

  &__heading {
    font-size: 20px;
    margin-bottom: 26px;
    font-family: var(--font-Inter);

    @include for-desktop {
      font-size: 18px;
      margin-bottom: 20px;
    }
  }

  &__list {
  }

  &__item {
    padding-left: 31px;
    background-repeat: no-repeat;
    background-position: 0 6px;
    background-size: 15px 19px;

    @include for-desktop {
      font-size: 15px;
      padding-left: 22px;
      background-size: 12px 16px;
    }

    &:not(:last-child) {
      margin-bottom: 19px;

      @include for-desktop {
        margin-bottom: 15px;
      }
    }
  }

  &__cell--img {
    width: 47%;

    @include tablet {
      width: 100%;
    }
  }

  &__image {

    @include tablet {
      width: 100%;
      height: 400px;
    }

    @include small-tablet {
      height: 300px;
    }
  }
}
