.novelties {
  background-color: var(--bg);
  padding: 80px 0 54px;

  &__subtile {
    margin-bottom: 62px;

    @include small-tablet {
      margin-bottom: 30px;
    }

  }

}

.swiper--novelties {
  margin-bottom: 52px;
}

.wrapper-grid {
  display: grid;
  justify-items: stretch;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 40px;

  @include xxx-desktop {
    gap: 20px;
  }
}

.card {
  width: 100%;
  max-width: 450px;
  grid-column: 4 span;
  display: flex;
  flex-direction: column;
  background-color: var(--white);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 88px 46px;
  border-radius: 10px;
  padding: 16px;
  padding-bottom: 25px;
  margin: 0 auto;

  @include big-desktop {
    background-size: 55px 32px;
  }

  @include tablet {
    max-width: 380px;
  }

  &__header {
    text-align: right;
    margin-bottom: 15px;
  }

  &__status {
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0px;
  }

  &__status--sold-out {
    color: var(--red);
  }

  &__status--stock {
    color: var(--green);
  }

  &__moodel {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    padding: 16px 0 23px;
  }

  &__model-name {
    flex: 1 1 auto;
    font-family: var(--font-Inter);
    color: var(--bl);
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;
    letter-spacing: 0.18px;
    text-overflow: ellipsis;
    margin-bottom: 24px;

    @include big-desktop {
      font-size: 16px;
    }
  }

  &__model-prices {
    color: var(--2-grey);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    letter-spacing: 0px;
  }

  &__old-price {
    color: var(--light-grey);
    font-family: var(--font-Inter);
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;
    text-decoration-line: line-through;
    margin-left: 4px;
    letter-spacing: 0px;
  }

  &__btn {
    width: -webkit-fill-available;
    border: 1px solid transparent;
    letter-spacing: 0px;
  }

  &__btn--track {
    background-color: transparent;
    border: 1px solid var(--orange);

    &:hover .card__btn-text--track {
      color: var(--white);
    }

    &:hover .cursor-click--track {
      fill: var(--white);
    }

  }

  &__btn-text {
    display: inline-block;
    margin-left: -7px;
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 123%;

  }

  &__btn-text--track {
    color: var(--orange);
    transition: color 0.3s ease-in-out;
  }

}

.view-link {
  display: block;
  width: fit-content;
  margin: auto;
  color: var(--2-grey);
  font-family: var(--font-Inter);
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  text-transform: uppercase;
  transition: color 0.3s ease-in-out;

  &:hover {
    color: var(--bl);
  }

}

.cursor-click {
  width: 25px;
  height: 25px;
  fill: var(--white);
  transition: fill 0.3s ease-in-out;
}

.cursor-click--track {
  fill: var(--orange);
}
