.mountain-bikes {
  background-color: var(--bg);
  padding: 72px 0 110px;

  &__subtitle {
    margin-bottom: 67px;

    @include big-desktop {
      margin-bottom: 30px;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    @include big-desktop {
      flex-direction: column;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 16px;
    margin-right: 20px;
    box-sizing: border-box;
    width: auto;

    @include big-desktop {
      grid-template-columns: repeat(12, 1fr);
      margin-right: 0;
      gap: 0;
    }
  }

  &__item {
    width: 100%;
    max-width: 302px;
    background-color: var(--dark);
    background-repeat: no-repeat;
    background-position: 202px 26px;
    background-size: 71px 71px;
    grid-column: 3 span;
    border-radius: 10px;
    padding: 31px 36px 32px 24px;

    @include big-desktop {
      max-width: none;
      background-position: 95% 15%;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    min-height: 64px;
    margin-bottom: 29px;
  }

  &__head {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
  }

  &__item-text {
    color: var(--white);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0px;
  }

  &__info {
    width: 100%;
    max-width: 509px;

    @include big-desktop {
      max-width: none;
      display: flex;
      align-items: center;
    }

    @include tablet {
      flex-direction: column;
    }
  }

  &__info-container {

    @include big-desktop {
      width: 50%;
    }

    @include tablet {
      width: 100%;
    }

  }

  &__bg {
    height: 257px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    margin-bottom: 31px;
    border-radius: 10px;

    @include big-desktop {
      width: 50%;
      margin-bottom: 0;
      margin-right: 30px;
      background-position: top center;
    }

    @include tablet {
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  &__text {
    color: var(--1-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 162%;
    margin-bottom: 39px;
    letter-spacing: 0px;
  }

  & .slider-container {
    overflow: hidden;
    margin-bottom: 50px;
  }

}
