.graph-modal {
  background-color: rgba($color: #000000, $alpha: 0.3);
  backdrop-filter: blur(3px);
}

.graph-modal__close {
  width: 26px;
  height: 24px;
  top: 40px;
  right: 40px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-delay: 0s;

  @include mobile {
    top: 30px;
    right: 30px;
  }

  &:hover {
    opacity: 0.7;
  }
}

.graph-modal__container--subscription {
  width: 100%;
  max-width: 525px;
  text-align: center;
  margin: auto;
  padding: 104px 25px 55px;

  @include small-tablet {
   max-width: 400px;
  }

  @include mobile {
    max-width: 330px;
    padding: 80px 25px 40px;
  }

}

.subscription-image {
  display: inline-block;
  margin: auto;
  margin-bottom: 32px;

  @include small-tablet {
    width: 70px;
    height: 70px;
    margin-bottom: 20px;
  }
}

.subscription-subtitle {
  color: var(--bl);
  text-align: center;
  font-family: var(--font-Bebas);
  font-size: 64px;
  font-style: normal;
  font-weight: 400;
  line-height: 123%;
  letter-spacing: 3.2px;

  @include small-tablet {
    font-size: 35px;
  }
}
